<template>
  <div class="map" id="map" :class="{active: fullMap}">
    <div class="map__buttons" :class="{active: sidebarOpen}">
      <button class="map__enlarge" @click="toggleFullMap">
        <img src="../assets/icons/enlarge.svg" alt="englarge map">
      </button>
      <button class="map__filter" @click="toggleSidebar"><i>Filters</i>
          <img src="../assets/icons/filter.svg" alt="toggle filters">
      </button>
      <button class="map__hotels" @click="toggleFullMap">Show Hotels</button>
    </div>  
    <div class="map__sidebar__wrapper" :class="{active: sidebarOpen}">
      <div class="map__sidebar">    
        <ul :key="filterSection.id" v-for="filterSection in Filters" class="filter-section">
          <li class="filter__header">{{filterSection.header}}</li>
          <li :key="filter.name" v-for="filter in filterSection.filters"><input type="checkbox"><div><div class="checkbox"></div>{{filter.name}}</div><span>{{filter.filterCount}}</span></li>
        </ul>
        <div class="filter__submit">
          <button @click="() => {toggleSidebar(); showFilters();}">Save Filters</button>
        </div>     
      </div>  
    </div>
  </div>
</template>

<script>
  import mapboxgl from "mapbox-gl";
  import Hotels from "../json/hotels.json";
  import Filters from "../json/filters.json";

  export default {
    name: 'Map',
    props: {},
    data() {
      return {
        Hotels,
        Filters,
        sidebarOpen: false,
        filtersActive: false,
        fullMap: false,
        paddingAmount: 312
      };
    },
    mounted(){
      mapboxgl.accessToken = 'pk.eyJ1IjoiYmVuZGF3c29uIiwiYSI6ImNrZ3oxcnh3cjBqMzEydHF2MXV4ZXA3aWoifQ.rnt1Wq6u6BqIyNPNoivTkA';   
      var map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/mapbox/light-v10',
        center: Hotels[0].longLat,
        zoom: 18,
        pitch: 60,
        bearing: 0,
        attributionControl: false        
      });  

      //TODO: find better way to expose map
      window.map = map;    
            
      Hotels.forEach(hotel => {
        const customMarker = document.createElement('div');
        const eyeIcon = document.createElement('div');
        customMarker.className = 'custom-marker';
        eyeIcon.className = 'eye-icon';
        customMarker.appendChild(eyeIcon);        

        var popup = new mapboxgl.Popup({ closeOnClick: true, offset: [0, -88] })
        .setHTML(`<div class="map__popup">
          <div class="map__popup-titles">
            <h4 class="map__popup-title">${hotel.name}</h4>
            <label><b> &dollar;${hotel.price}</b> / Avg night</label>
          </div>          
          <div class="map__popup-button">
            <label>VIEW HOTEL</label>
          </div>
        </div>`);    

        let marker = new mapboxgl.Marker(customMarker).setLngLat(hotel.longLat).setPopup(popup).addTo(map);   
        popup._content.querySelector(".map__popup-button").addEventListener("click", () => alert("view hotel"));
        customMarker.addEventListener("click", () => {
          let markers = document.querySelectorAll(".custom-marker");
          markers.forEach(eye => eye.classList.remove("active"));
          customMarker.classList.add("active");
        });
      });

    },
    methods: {
      toggleSidebar(){
        this.sidebarOpen = !this.sidebarOpen;
        window.map.easeTo({padding: {right: this.sidebarOpen ? this.paddingAmount : 0}, duration:600});       
      },
      showFilters(){       
        this.filtersActive = !this.filtersActive;
        this.$emit("filtersChanged", this.filtersActive);
      },
      toggleFullMap(){
        this.sidebarOpen = false;
        this.fullMap = !this.fullMap;
        //TODO: not great, find a better way to update map size while animating to/from full view
        const animateMap = setInterval(() => window.map.resize(), 1);
        setTimeout(() => clearInterval(animateMap), 605);
      }
    }
  }
</script>

<style src='mapbox-gl/dist/mapbox-gl.css'></style>
