      }     
      .map__popup-button{text-align: center; background-color: $mapMarkerColor; padding:9px 20px; border-radius:0 0 10px 10px; cursor: pointer;
        label{@include font(14, $inter, 18, 0, 700, $grey34); cursor: pointer;}
