<template>
  <li class="hotel" @click="panToHotel(hotel.longLat, hotel.id)">    
    <div>
      <HotelCarousel :images="hotel.images" class="hotel__left"/>  
    </div>
    
    <div class="hotel__right">
      <img id="favourite" src="../assets/icons/heart.svg" alt="favourite">
        <h4>{{hotel.name}}</h4>   
        <label><img src="../assets/icons/hotel-marker.svg" alt="location marker" >{{hotel.distance}}km from {{hotel.near}}</label>
        <p>{{hotel.description}}</p>
        <div class="hotel__info">
          <label><img src="../assets/icons/star.svg" alt="hotel rating" ><b>{{hotel.rating}}</b>({{hotel.reviews}} Reviews)</label>
          <label><b>&dollar;{{hotel.price}}</b> <span>/ Avg night</span></label>
        </div>
    </div>
  </li>
</template>

<script>
  import HotelCarousel from '@/components/HotelCarousel.vue';

  export default {
    name: 'Hotel',
    components: {HotelCarousel},
    props: {
      hotel: Object
    },   
    data() {
      return {
        mapBearing: 0,
        lastClicked: 0
      };
    }, 
    methods: {      
      panToHotel(longLat, id){
        window.map.flyTo({center: longLat, speed: 0.4, curve: 0.4, bearing: id !== this.lastClicked ? this.mapBearing += 40 :  this.mapBearing = 0});      
        this.lastClicked = id;        
      }
    }
  }
</script>

